/*******************************************************************************
* Rules for the UX/UI of sidebar sliding drawers on mobile
* Note that this sheet controls styles across many parts of the theme
* It is aggregated into this one sheet instead of being split across
* components in order to keep it easier to debug in one place.
* It is broken up into major sections below.
*/
/*******************************************************************************
* Sidebar drawer behavior
*/
/**
 * NOTE: We use this mixin to define the toggle behavior on narrow screens,
 * And the wide-screen behavior of the sections is defined in their own section
 * .scss files.
 */
.bd-sidebar::backdrop {
  background-color: black;
  opacity: 0.5;
}

@media (max-width: 959.98px) {
  .bd-sidebar-primary {
    position: fixed;
    top: 0;
    z-index: 1055;
    height: 100vh;
    max-height: 100vh;
    width: 75%;
    flex-grow: 0.75;
    max-width: 350px;
    transition: visibility 200ms ease-out, margin 200ms ease-out;
    visibility: hidden;
    border: 0;
    margin-left: -75%;
    left: 0;
  }
}
.bd-sidebar-primary[open] {
  margin-left: 0;
  visibility: visible;
}

@media (max-width: 1199.98px) {
  .bd-sidebar-secondary {
    position: fixed;
    top: 0;
    z-index: 1055;
    height: 100vh;
    max-height: 100vh;
    width: 75%;
    flex-grow: 0.75;
    max-width: 350px;
    transition: visibility 200ms ease-out, margin 200ms ease-out;
    visibility: hidden;
    border: 0;
    margin-right: -75%;
    right: 0;
  }
}
.bd-sidebar-secondary[open] {
  margin-right: 0;
  visibility: visible;
}

