  content: var(--pst-icon-admonition-seealso);
}
div.admonition.admonition-todo,
.admonition.admonition-todo {
  border-color: var(--pst-color-secondary);
}
div.admonition.admonition-todo > .admonition-title,
.admonition.admonition-todo > .admonition-title {
  background-color: var(--pst-color-secondary-bg);
}
div.admonition.admonition-todo > .admonition-title::after,
.admonition.admonition-todo > .admonition-title::after {
  color: var(--pst-color-secondary);
  content: var(--pst-icon-admonition-todo);
}
div.admonition,
.admonition {
  /**
   * Special-case for a `sidebar` class that makes the admonition float to
   * the right like the {sidebar} directive.
   */
}
div.admonition.sidebar,
.admonition.sidebar {
  max-width: 40%;
  float: right;
  clear: both;
  margin-left: 0.5rem;
  margin-top: 0;
  border-width: 0 0 0 0.2rem;
}
div.admonition.sidebar.attention, div.admonition.sidebar.important,
.admonition.sidebar.attention,
.admonition.sidebar.important {
  border-color: var(--pst-color-attention);
}
div.admonition.sidebar.caution, div.admonition.sidebar.warning,
.admonition.sidebar.caution,
.admonition.sidebar.warning {
  border-color: var(--pst-color-warning);
}
div.admonition.sidebar.danger, div.admonition.sidebar.error,
.admonition.sidebar.danger,
.admonition.sidebar.error {
  border-color: var(--pst-color-danger);
}
div.admonition.sidebar.hint, div.admonition.sidebar.tip, div.admonition.sidebar.seealso,
.admonition.sidebar.hint,
.admonition.sidebar.tip,
.admonition.sidebar.seealso {
  border-color: var(--pst-color-success);
}
div.admonition.sidebar.note, div.admonition.sidebar.todo,
.admonition.sidebar.note,
.admonition.sidebar.todo {
  border-color: var(--pst-color-info);
}
div.admonition.sidebar p.admonition-title ~ *,
.admonition.sidebar p.admonition-title ~ * {
  margin-left: 0;
  margin-right: 0;
}

/**************************************************************
 * Similar content blocks that are not technically admonitions.
 */
/**
 * Topics and the {contents} directive
 */
div.topic,
div.topic.contents,
nav.contents,
aside.topic {
  display: flex;
  flex-direction: column;
  background-color: var(--pst-color-surface);
  border-color: var(--pst-color-border);
  border-radius: 0.25rem;
  padding: 1rem 1.25rem;
  box-shadow: 0 0.2rem 0.5rem var(--pst-color-shadow), 0 0 0.0625rem var(--pst-color-shadow) !important;
}
div.topic .topic-title,
div.topic.contents .topic-title,
nav.contents .topic-title,
aside.topic .topic-title {
  margin: 0 0 0.5rem;
}
div.topic p,
div.topic.contents p,
nav.contents p,
aside.topic p {
  color: var(--pst-color-on-surface) !important;
}
div.topic ul.simple,
div.topic.contents ul.simple,
nav.contents ul.simple,
aside.topic ul.simple {
  padding-left: 1rem;
}
div.topic ul.simple ul,
div.topic.contents ul.simple ul,
nav.contents ul.simple ul,
aside.topic ul.simple ul {
  padding-left: 2em;
}

/**
 * Sidebar directive
 */
aside.sidebar {
  border: 1px solid var(--pst-color-border);
  background-color: var(--pst-color-surface);
  border-radius: 0.25rem;
  margin-left: 0.5rem;
  padding: 0;
}
aside.sidebar > *:last-child {
  padding-bottom: 1rem;
}
aside.sidebar p.sidebar-title {
  position: relative;
  margin-bottom: 0;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid var(--pst-color-border);
  font-family: var(--pst-font-family-heading);
  font-weight: var(--pst-admonition-font-weight-heading);
}
aside.sidebar > *:not(.sidebar-title):first-child,
aside.sidebar > p.sidebar-title + * {
  margin-top: 1rem;
}
aside.sidebar > * {
  padding-left: 1rem;
  padding-right: 1rem;
}

/**
 * Rubrics look kind of like section headers
 */
p.rubric {
  display: flex;
  flex-direction: column;
}

/**
 * Seealso is kind of like a vertically-collapsed admonition
 */
.seealso dd {
  margin-top: 0;
  margin-bottom: 0;
}

/*******************************************************************************
* Styling for field lists
*/
/* grey highlighting of 'parameter' and 'returns' field */
table.field-list {
  border-collapse: separate;
  border-spacing: 10px;
  margin-left: 1px;
}
table.field-list th.field-name {
  padding: 1px 8px 1px 5px;
  white-space: nowrap;
  background-color: var(--pst-color-surface);
}
table.field-list {
  /* italic font for parameter types */
}
table.field-list td.field-body p {
  font-style: italic;
}
table.field-list td.field-body p > strong {
  font-style: normal;
}
table.field-list td.field-body {
  /* reduced space around parameter description */
}
table.field-list td.field-body blockquote {
  border-left: none;
  margin: 0 0 0.3em;
  padding-left: 30px;
}

/*******************************************************************************
* Styling for autosummary tables
*/
.table.autosummary td:first-child {
  white-space: nowrap;
}

/* overriding basic.css to use our own monospace font */
.sig {
  font-family: var(--pst-font-family-monospace);
}

/* C++ specific styling - overriding the basic.css to avoid custom colors */
.sig-inline.c-texpr,
.sig-inline.cpp-texpr {
  font-family: unset;
}

.sig.c .k,
.sig.c .kt,
.sig.cpp .k,
.sig.cpp .kt {
  color: var(--pst-color-text-base);
}

.sig.c .m,
.sig.cpp .m {
  color: var(--pst-color-text-base);
}

.sig.c .s,
.sig.c .sc,
.sig.cpp .s,
.sig.cpp .sc {
  color: var(--pst-color-text-base);
}

.sig-name {
  color: var(--pst-color-inline-code);
}

.sig-param .o,
.sig-param .default_value {
  color: var(--pst-color-text-muted);
  font-weight: normal;
}

dt:target,
span.highlighted {
  background-color: var(--pst-color-target);
}

.viewcode-back {
  font-family: var(--pst-font-family-base);
}

.viewcode-block:target {
  border-top: 1px solid var(--pst-color-border);
  border-bottom: 1px solid var(--pst-color-border);
  position: relative;
  background-color: var(--pst-color-target);
}

dl > dt > a:has(.viewcode-link) {
  float: right;
}
dl > dt > a:has(.viewcode-link) .viewcode-link {
  float: none;
}

/*******************************************************************************
* Styling for autosummary titles like "parameters" and "returns"
*/
dl[class]:not(.option-list, .field-list, .footnote, .glossary, .simple) {
  margin-bottom: 3rem;
}
dl[class]:not(.option-list, .field-list, .footnote, .glossary, .simple) dd {
  margin-left: 2rem;
}
dl[class]:not(.option-list, .field-list, .footnote, .glossary, .simple) dd > dl.simple > dt {
  display: flex;
}
dl[class]:not(.option-list, .field-list, .footnote, .glossary, .simple) dl.field-list {
  display: grid;
  grid-template-columns: unset;
}
dl[class]:not(.option-list, .field-list, .footnote, .glossary, .simple) dt.field-odd,
dl[class]:not(.option-list, .field-list, .footnote, .glossary, .simple) dt.field-even {
  margin-top: 0.2rem;
  margin-bottom: 0.1rem;
  background-color: var(--pst-color-surface);
}

/**
 * Code block styling
 * Note that we inherit a lot of styling from Bootstrap so not many rules here.
 */
div[class*=highlight-],
div.highlight,
div.literal-block-wrapper {
  display: flex;
  flex-direction: column;
  width: unset;
  border-radius: 0.25rem;
  break-inside: avoid;
}

div.literal-block-wrapper {
  border: 1px solid var(--pst-color-border);
  border-radius: 0.25rem;
}
div.literal-block-wrapper div.code-block-caption {
  margin: 0;
  border-bottom: 1px solid var(--pst-color-border);
  padding: 0.5rem;
  font-size: 1rem;
  font-weight: var(--pst-font-weight-caption);
}
div.literal-block-wrapper div.code-block-caption a.headerlink {
  font-size: inherit;
}
div.literal-block-wrapper div[class*=highlight-] {
  margin: 0;
  border-radius: 0;
}
div.literal-block-wrapper div[class*=highlight-] pre {
  border: none;
  box-shadow: none;
}

/**
 * In-line code
 */
code.literal {
  padding: 0.1rem 0.25rem;
  background-color: var(--pst-color-surface);
  border: 1px solid var(--pst-color-border);
  border-radius: 0.25rem;
}

a > code {
  color: var(--pst-color-inline-code-links);
}
a > code:hover {
  color: var(--pst-color-link-hover);
}

span.linenos {
  opacity: 0.8 !important;
}

figure > a,
figure > a > img,
figure > img,
figure > video {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

figure a.headerlink {
  position: absolute;
  font-size: inherit;
}
figure:hover a.headerlink {
  visibility: visible;
}
figure figcaption {
  font-family: var(--pst-font-family-heading);
  font-weight: var(--pst-font-weight-caption);
  color: var(--pst-color-text-muted);
  margin-left: auto;
  margin-right: auto;
  margin-top: 0.3rem;
  text-align: center;
}
figure figcaption > p:last-child {
  margin-bottom: 0;
}
figure figcaption p {
  text-align: start;
  display: inline-block;
}
figure figcaption table.table {
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
}

dt.label > span.brackets:not(:only-child)::before {
  content: "[";
}

dt.label > span.brackets:not(:only-child)::after {
  content: "]";
}

a.footnote-reference {
  vertical-align: super;
  font-size: small;
}

aside.footnote {
  margin-bottom: 0.5rem;
}
aside.footnote:last-child {
  margin-bottom: 1rem;
}
aside.footnote span.label,
aside.footnote span.backrefs {
  font-weight: bold;
}
aside.footnote:target {
  background-color: var(--pst-color-target);
}

/**
 * Hacky fixes that don't fit cleanly into other sections
 */
table.highlighttable td.linenos,
span.linenos,
div.doctest > div.highlight span.gp {
  /* gp: Generic.Prompt */
  user-select: none;
}

dd {
  margin-top: 3px;
  margin-bottom: 10px;
  margin-left: 30px;
}

ol,
ul {
  padding-inline-start: 2rem;
}
ol li > p:first-child,
ul li > p:first-child {
  margin-bottom: 0.25rem;
  margin-top: 0.25rem;
}

blockquote {
  padding: 1em;
  color: var(--pst-color-text-muted);
  border-left: 0.25em solid var(--pst-color-blockquote-notch);
  border-radius: 0.25rem;
  position: relative;
}
blockquote p {
  color: var(--pst-color-text-base);
}
blockquote .line-block {
  margin: 0;
}
blockquote p:last-child {
  margin-bottom: 0;
}
blockquote::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  pointer-events: none;
}
blockquote {
  background-color: var(--pst-color-surface);
}
blockquote a {
  color: var(--pst-color-inline-code-links);
}
blockquote::before {
  z-index: -1;
}

/**
 * Span-level styling within content
 */
span.guilabel {
  border: 1px solid var(--pst-color-info);
  font-size: 80%;
  font-weight: 700;
  border-radius: 4px;
  padding: 2.4px 6px;
  margin: auto 2px;
  position: relative;
}
span.guilabel::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  pointer-events: none;
}
span.guilabel {
  background-color: var(--pst-color-info-bg);
}

a.reference.download::before {
  content: var(--pst-icon-download);
  font: var(--fa-font-solid);
  font-size: 0.8em;
  padding: 0 0.25em;
  color: var(--pst-color-text-muted);
}

/**
 * Tables
 */
table {
  margin-left: auto;
  margin-right: auto;
}
table.table-right {
  margin-right: 0;
}
table.table-left {
  margin-left: 0;
}

table caption {
  text-align: center;
  caption-side: top;
  color: var(--pst-color-text-muted);
}

th.text-left,
td.text-left {
  text-align: left;
}
th.text-right,
td.text-right {
  text-align: right;
}
th.text-center,
td.text-center {
  text-align: center;
}

.table {
  color: var(--pst-color-table);
  border: 1px solid var(--pst-color-table-outer-border);
}
.table th ~ th,
.table th ~ td,
.table td ~ th,
.table td ~ td {
  border-left: 1px solid var(--pst-color-table-inner-border);
}
.table thead tr {
  background-color: var(--pst-color-table-heading-bg);
  border-bottom: 2px solid var(--pst-color-primary);
}
.table tbody tr:nth-child(odd) {
  background-color: var(--pst-color-table-row-zebra-low-bg);
}
.table tbody tr:nth-child(even) {
  background-color: var(--pst-color-table-row-zebra-high-bg);
}
.table {
  --bs-table-bg: transparent;
  --bs-table-color: var(
    --pst-color-text-base
  );
}

.pst-scrollable-table-container {
  overflow-x: auto;
}

/**
 * Style the toctree component in pages (avoid modifying the navbars)
 */
.toctree-wrapper p.caption {
  font-size: 1.5em;
  margin-bottom: 0;
}
.toctree-wrapper > ul {
  padding-left: 0;
}
.toctree-wrapper li[class^=toctree-l] {
  list-style: none;
  margin-bottom: 0.2em;
}
.toctree-wrapper li[class^=toctree-l] > a {
  list-style: none;
  font-size: 1.1em;
}
.toctree-wrapper li[class^=toctree-l] > ul {
  list-style: none;
  padding-inline-start: 1.5em;
}
.toctree-wrapper .toctree-l1 > a {
  font-size: 1.3em;
}

div.topic.contents ul.simple,
nav.contents ul.simple {
  list-style: none;
  padding-left: 0;
}

/**
 * Mathematics via MathJax.
 *
 * This is designed for MathJax v3
 * ref: https://www.sphinx-doc.org/en/master/usage/extensions/math.html#module-sphinx.ext.mathjax
 */
span.math,
div.math {
  align-items: center;
  display: flex;
  max-width: 100%;
  overflow: hidden;
}

span.math {
  display: inline-flex;
}

div.math {
  gap: 0.5em;
  flex-direction: row-reverse;
}
div.math span.eqno a.headerlink {
  position: relative;
  font-size: 1em;
}
div.math mjx-container {
  flex-grow: 1;
  padding-bottom: 0.2rem;
  overflow: auto;
}
div.math mjx-container mjx-assistive-mml {
  height: 0;
}

/**
 * ABlog
 * ref: https://ablog.readthedocs.io/
 */
/**
 * Sidebar template components
 */
.ablog-sidebar-item h2, .ablog-sidebar-item .h2,
.ablog-sidebar-item h3,
.ablog-sidebar-item .h3 {
  font-size: var(--pst-sidebar-header-font-size);
  margin-top: 0.5rem;
}
.ablog-sidebar-item h2 a, .ablog-sidebar-item .h2 a,
.ablog-sidebar-item h3 a,
.ablog-sidebar-item .h3 a {
  color: var(--pst-color-text-base);
}
.ablog-sidebar-item ul {
  list-style: none;
  padding-left: 0;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  margin-bottom: 0;
}
.ablog-sidebar-item ul.ablog-cloud {
  flex-flow: row wrap;
  gap: 0.5rem;
}
.ablog-sidebar-item ul.ablog-cloud li {
  display: flex;
  align-items: center;
}

/**
 * Previous / next buttons at the bottom
 */
.ablog__prev-next {
  font-size: 1.2em;
  display: flex;
  padding: 1rem 0;
}
.ablog__prev-next > span {
  display: flex;
  max-width: 45%;
}
.ablog__prev-next > span a {
  display: flex;
  align-items: center;
  margin-left: auto;
  gap: 1rem;
  line-height: 1.5rem;
}
.ablog__prev-next > span a i::before {
  color: var(--pst-color-text-base);
}
.ablog__prev-next span.ablog__prev i.fa-arrow-circle-left::before {
  content: var(--pst-icon-angle-left);
}
.ablog__prev-next span.ablog__spacer {
  display: none;
}
.ablog__prev-next span.ablog__next {
  margin-left: auto;
  text-align: right;
}
.ablog__prev-next span.ablog__next i.fa-arrow-circle-right::before {
  content: var(--pst-icon-angle-right);
}

/**
 * {postlist} directive and posts page
 */
.ablog__collection,
.postlist {
  padding-left: 0;
}
.ablog__collection .ablog-post,
.postlist .ablog-post {
  list-style: none;
}
.ablog__collection .ablog-post .ablog-archive,
.postlist .ablog-post .ablog-archive {
  display: flex;
  flex-flow: row wrap;
  gap: 1rem;
  list-style: none;
  font-size: 0.75rem;
  padding-left: 0;
}
.ablog__collection .ablog-post .ablog-post-title,
.postlist .ablog-post .ablog-post-title {
  margin-top: 0;
  font-size: 1.25rem;
}
.ablog__collection .ablog-post .ablog-post-title a,
.postlist .ablog-post .ablog-post-title a {
  font-weight: bold;
}
.ablog__collection .ablog-post .ablog-post-expand,
.postlist .ablog-post .ablog-post-expand {
  margin-bottom: 0.5rem;
}

/**
 * Special cases for Bootstrap functionality
 */
.docutils.container {
  padding-left: unset;
  padding-right: unset;
  margin-left: unset;
  margin-right: unset;
  max-width: unset;
  width: unset;
}

.btn {
  --bs-btn-focus-box-shadow: 0 0 0 0.1875rem var(--pst-color-accent);
}

/**
 * Sphinx Copybutton
 * ref: https://sphinx-copybutton.readthedocs.io/
 */
div.highlight button.copybtn {
  display: flex;
  align-items: center;
  justify-content: center;
}
div.highlight button.copybtn:not(.success) {
  color: var(--pst-color-muted);
}
div.highlight button.copybtn {
  border: none;
  background-color: var(--pst-color-surface);
}
div.highlight button.copybtn:hover:not(.success) {
  color: var(--pst-color-text);
  background-color: var(--pst-color-shadow);
}
div.highlight button.copybtn.o-tooltip--left::after {
  color: var(--pst-color-text);
  background-color: var(--pst-color-surface);
}
div.highlight button.copybtn:focus {
  opacity: 1;
}
div.highlight button.copybtn:focus-visible {
  outline: var(--pst-color-accent) solid 0.1875rem;
}

div.highlight:has(button.copybtn) {
  min-height: 2em;
}

#ethical-ad-placement .ethical-sidebar a,
#ethical-ad-placement .ethical-sidebar a:visited,
#ethical-ad-placement .ethical-sidebar a:hover,
#ethical-ad-placement .ethical-sidebar a:active,
#ethical-ad-placement .ethical-footer a,
#ethical-ad-placement .ethical-footer a:visited,
#ethical-ad-placement .ethical-footer a:hover,
#ethical-ad-placement .ethical-footer a:active {
  color: var(--pst-color-text-base);
}
#ethical-ad-placement .ethical-sidebar,
#ethical-ad-placement .ethical-footer {
  background-color: var(--pst-color-background);
  border: 1px solid var(--pst-color-border);
  border-radius: 5px;
  color: var(--pst-color-text-base);
  font-size: 14px;
  line-height: 20px;
}

/**
 * Styles for various Sphinx execution libraries.
 * For now, where these define output sections, we simply revert their background
 * to be a "light theme" background. This ensures that inputs/outputs behave similarly,
 * because the CSS is often controlled by each package.
 * In the future, we might add dark theme support for specific packages.
 */
/******************************************************************************
 * Jupyter Sphinx
 */
.bd-content div.jupyter_container {
  border: none;
  background-color: unset;
  box-shadow: none;
}
.bd-content div.jupyter_container div.output,
.bd-content div.jupyter_container div.highlight {
  border-radius: 0.25rem;
}
.bd-content div.jupyter_container div.highlight {
  background-color: var(--pst-color-surface);
}
.bd-content div.jupyter_container .cell_input,
.bd-content div.jupyter_container .cell_output {
  border-radius: 0.25rem;
}
.bd-content div.jupyter_container .cell_input pre,
.bd-content div.jupyter_container .cell_output pre {
  padding: 1rem;
}

/* Styles for graphviz generated output from Sphinx */
/* Style the inheritance diagram such that it has a dark mode */
html[data-theme=dark] div.graphviz > object.inheritance {
  filter: brightness(0.8) invert(0.82) contrast(1.2);
  color-scheme: normal;
}

/**
 * Special-cases for packages in the PyData ecosystem
 */
.xr-wrap[hidden] {
  display: block !important;
}

.jp-OutputArea-output.lm-Widget {
  overflow: auto;
}

/*******************************************************************************
* master color map. Only the colors that actually differ between light and dark
* themes are specified separately.
*
* To see the full list of colors see https://www.figma.com/file/rUrrHGhUBBIAAjQ82x6pz9/PyData-Design-system---proposal-for-implementation-(2)?node-id=1234%3A765&t=ifcFT1JtnrSshGfi-1
*/
/**
* Function to get items from nested maps
*/
/* Assign base colors for the PyData theme */
:root {
  --pst-teal-50: #f4fbfc;
  --pst-teal-100: #e9f6f8;
  --pst-teal-200: #d0ecf1;
  --pst-teal-300: #abdde6;
  --pst-teal-400: #3fb1c5;
  --pst-teal-500: #0a7d91;
  --pst-teal-600: #085d6c;
