/**
 * Sphinx togglebutton
 *
 * The rules in this style sheet are meant to tweak the
 * [sphinx-togglebutton](https://sphinx-togglebutton.readthedocs.io/en/latest/index.html)
 * extension so that it matches the look and feel of this theme.
 */
.bd-content .dropdown.admonition.toggle .admonition-title:hover .tb-icon {
  opacity: 1;
  scale: 1.1;
}
.bd-content .dropdown.admonition.toggle .admonition-title .tb-icon {
  opacity: 0.6;
}
.bd-content .dropdown.admonition.toggle .admonition-title:hover {
  filter: brightness(0.9);
}
html[data-theme=dark] .bd-content .dropdown.admonition.toggle .admonition-title:hover {
  filter: brightness(1.1);
}
.bd-content .dropdown.admonition.toggle .admonition-title:hover {
  box-shadow: none;
}
.bd-content .dropdown.admonition.toggle button.toggle-button {
  color: inherit;
}
.bd-content .dropdown.admonition.toggle button.toggle-button.toggle-button-hidden .toggle-chevron-right {
  transform: rotate(90deg);
  transition: none;
}
.bd-content .dropdown.admonition.toggle button.toggle-button .toggle-chevron-right {
  transform: rotate(-90deg);
  transition: none;
}
.bd-content .dropdown.admonition.toggle:has(:focus-visible) {
  /* Override Sphinx Toggle Button. Make the overflow visible, otherwise the
  focus ring is hidden. */
  overflow: visible;
  /*
  Why not just do the following?

  ```
  .admonition-title {
    outline: $focus-ring-outline;
  }
  ```

  Why use ::before? If we put the focus ring on the ::before pseudo-element,
  we can reposition the focus ring by repositioning the pseudo-element.

  Why reposition? The left edge of the admonition title box does not align
  with the left edge of the overall admonition box. There is a left border
  that belongs to the overall box. The border is outside of the admonition
  title, which means it is also outside of a focus ring around the title. We
  can make the focus ring bigger, with `outline-offset`, but this will
  result in a ring that looks off-centered. So we have to pull the ring left
  and stretch it right. That's what the pseudo-element allows us to do.

  Note: we do not have to do this for collapsible admonitions made with
  Sphinx Design (as opposed to sphinx-togglebutton).
  */
}
.bd-content .dropdown.admonition.toggle:has(:focus-visible) .admonition-title::before {
  content: "";
  left: -0.2rem;
  width: calc(100% + 0.2rem);
  height: 100%;
  outline: var(--pst-color-accent) solid 0.1875rem;
  outline-offset: 0.125rem;
  border-radius: 0.1875rem;
}
.bd-content .dropdown.admonition.toggle:has(:focus-visible):not(.toggle-hidden) .admonition-title::before {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.bd-content details.toggle-details summary {
  border-left: 3px solid var(--pst-color-primary);
}
.bd-content details.toggle-details summary .toggle-chevron-right {
  transform: rotate(90deg);
  transition: none;
}
.bd-content details.toggle-details summary:hover .tb-icon {
  opacity: 1;
  scale: 1.1;
}
.bd-content details.toggle-details summary .tb-icon {
  opacity: 0.6;
}
.bd-content details.toggle-details summary:hover {
  filter: brightness(0.9);
}
html[data-theme=dark] .bd-content details.toggle-details summary:hover {
  filter: brightness(1.1);
}
.bd-content details.toggle-details summary:hover {
  box-shadow: none;
}
.bd-content details.toggle-details summary:focus-visible {
  outline-offset: 0.125rem;
}
.bd-content details.toggle-details[open] .toggle-chevron-right {
  transform: rotate(-90deg);
  transition: none;
}
.bd-content details.toggle-details[open] :focus-visible {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

/**
 * Styles for various Sphinx execution libraries to display pre-executed notebooks.
 * For now, where these define output sections, we simply revert their background
 * to be a "light theme" background. This ensures that inputs/outputs behave similarly,
 * because the CSS is often controlled by each package.
 * In the future, we might add dark theme support for specific packages.
 */
/*******************************************************************************
 * nbsphinx
 */
html div.rendered_html,
html .jp-RenderedHTMLCommon {
