}
html div.rendered_html table,
html .jp-RenderedHTMLCommon table {
  table-layout: auto;
}

.bd-content .nboutput .output_area.rendered_html table.dataframe,
.bd-content .nboutput .output_area .jp-RenderedHTMLCommon table.dataframe {
  color: var(--pst-color-table);
  border: 1px solid var(--pst-color-table-outer-border);
