.bd-content .nboutput .output_area.rendered_html table.dataframe th ~ th,
.bd-content .nboutput .output_area.rendered_html table.dataframe th ~ td,
.bd-content .nboutput .output_area.rendered_html table.dataframe td ~ th,
.bd-content .nboutput .output_area.rendered_html table.dataframe td ~ td,
.bd-content .nboutput .output_area .jp-RenderedHTMLCommon table.dataframe th ~ th,
.bd-content .nboutput .output_area .jp-RenderedHTMLCommon table.dataframe th ~ td,
.bd-content .nboutput .output_area .jp-RenderedHTMLCommon table.dataframe td ~ th,
.bd-content .nboutput .output_area .jp-RenderedHTMLCommon table.dataframe td ~ td {
  border-left: 1px solid var(--pst-color-table-inner-border);
}
.bd-content .nboutput .output_area.rendered_html table.dataframe thead tr,
.bd-content .nboutput .output_area .jp-RenderedHTMLCommon table.dataframe thead tr {
  background-color: var(--pst-color-table-heading-bg);
