}
.bd-content .nboutput .output_area.rendered_html table.dataframe tbody tr:hover,
.bd-content .nboutput .output_area .jp-RenderedHTMLCommon table.dataframe tbody tr:hover {
  background-color: var(--pst-color-table-row-hover-bg);
}
html[data-theme=dark] .bd-content .nboutput .output_area.rendered_html:not(:has(table.dataframe)),
html[data-theme=dark] .bd-content .nboutput .output_area .widget-subarea {
  color: var(--pst-color-on-background);
  background-color: var(--pst-color-text-base);
  border-radius: 0.25rem;
  padding: 0.5rem;
}
html[data-theme=dark] .bd-content .nboutput .output_area.stderr {
