.bd-content div.cell_output table.dataframe th ~ td,
.bd-content div.cell_output table.dataframe td ~ th,
.bd-content div.cell_output table.dataframe td ~ td {
  border-left: 1px solid var(--pst-color-table-inner-border);
}
.bd-content div.cell_output table.dataframe thead tr {
  background-color: var(--pst-color-table-heading-bg);
  border-bottom: 2px solid var(--pst-color-primary);
}
